import * as React from "react";
import Button from 'antd/lib/button';import 'antd/lib/button/style/css.js';import Modal from 'antd/lib/modal';import 'antd/lib/modal/style/css.js';import Input from 'antd/lib/input';import 'antd/lib/input/style/css.js';import Form from 'antd/lib/form';import 'antd/lib/form/style/css.js';import message from 'antd/lib/message';import 'antd/lib/message/style/css.js';
import { observer, inject } from "mobx-react";
import IsDelte from "../../../components/dloag";
import { FormComponentProps } from "antd/lib/form";
import "./css/classroom.css";
interface UserFormProps extends FormComponentProps {
  age: number;
  name: string;
  history: any;
  user: any;
  form: any;
  classroom: any;
}
@inject("classroom")
@observer
class ClassRoom extends React.Component<UserFormProps, any> {
  state = {
    questiontypes: [],
    visible: false,
    len: 0,
    mangerroom: []
  };
  componentDidMount() {
    // 
    this.getMangerRooms();
  }

  // 
  getMangerRooms = async () => {
    const mangerroom = await this.props.classroom.getMangerRoom();
    this.setState({ mangerroom });
  };

  // 
  handleOk = () => {
    this.setState({ loading: true });
  };

  // 
  showModal = () => {
    this.setState({
      visible: true
    });
  };

  //  x 
  handleCancel = () => {
    this.setState({ visible: false });
  };

  // 
  addManger = async (str: string) => {
    const msg = await this.props.classroom.addMangerRoom({ room_text: str });
    msg == '' ? message.success(msg) : message.warning(msg);
    this.setState({ visible: false }); 
    this.getMangerRooms();
  };

  //  
  DeleteMangerRoom = async (str:any) => {
    const msg = await this.props.classroom.DelteMangerRoom(str);
    message.success(msg);
    this.getMangerRooms();
  };

  //  
  DeleteIsManger = (id: string) => {
    IsDelte(this.DeleteMangerRoom,{'room_id':id});
  };

  //  
  handleSubmit = async (e: any) => {
    e.preventDefault();
    this.props.form.validateFields((err: any, values: any) => {
      if (!err) {
        this.addManger(values.classNames);
      }
    });
  };

  public render() {
    const { visible, mangerroom } = this.state;
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="main">
        <h2 className="titType"></h2>
        <div className="typesContent">
          <div className="tableType">
            <div className="addtype">
              <Button type="primary" className="btnsa" onClick={this.showModal}>
                +
              </Button>
            </div>
            <table className="tableType tableTypeText" style={{ width: "100%" }}>
              <thead>
                <tr className="tabletr tableTypeText">
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {
                  mangerroom && mangerroom.map((el: any) => (
                    <tr key={el.room_id}>
                      <td>{el.room_text}</td>
                      <td className="tabletrDelte" onClick={() => {this.DeleteIsManger(el.room_id);}}></td>
                    </tr>
                  ))
                }
              </tbody>
            </table>
          </div>
        </div>
        <Modal visible={visible} title="" onOk={this.handleOk} onCancel={this.handleCancel} centered={true} destroyOnClose={true} keyboard footer={null}>
          <Form  labelCol={{ span: 5 }}  wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit} >
              <Form.Item label=""> {getFieldDecorator("classNames", {
                  rules: [{ required: true, message: "" }]
                })(<Input placeholder="" />)}
              </Form.Item>
              <Form.Item wrapperCol={{ span: 12, offset: 5 }}>
                <Button type="primary" htmlType="submit"></Button>
              </Form.Item>
          </Form>
        </Modal>
      </div>
    );
  }
}

export default Form.create()(ClassRoom);



